@keyframes profile_animate{
  0%{
      border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
  50%{
      border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
  }
  100%{
      border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
}

.square {
  width: 70px;
  height: 70px;
  font-size: 3em;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: inherit;
  backdrop-filter: 2px;
  border: 2px solid rgb(124, 157, 150);
  color: rgb(124, 157, 150);
  cursor: pointer;
}

.board-row {
  display: flex;
}

.board-row:first-child .square {
  border-top: none;
}

.board-row:last-child .square {
  border-bottom: none;
}

.square:first-child {
  border-left: none;
}

.square:last-child {
  border-right: none;
}

/* Mobile devices */
@media only screen and (max-width: 767px) {
  .game .square {
      width: 50px !important; 
      height: 50px !important;
      font-size: 2.5em !important;
  }

  .game-info {
    font-size: 16px;
    gap: 1rem;
  }

  .game-board {
    margin: 0 10px 0 0;
  }
}
