:root {
    --base-spacing: 1rem;

    /* Spacing scale */
    --space-xs: 0.25rem;
    --space-sm: 0.5rem;
    --space-md: 1rem;
    --space-lg: 1.5rem;
    --space-xl: 2rem;
    
    /* Typography */
    --font-size-xs: 0.75rem;
    --font-size-sm: 0.875rem;
    --font-size-md: 1rem;
    --font-size-lg: 1.25rem;
    --font-size-xl: 1.5rem;
    --font-size-2xl: 2rem;
    --font-size-3xl: 2.25rem;
    
    /* Colors */
    --color-primary: rgb(124, 157, 150);
    --color-secondary: rgb(236, 187, 143);
    --color-text: wheat;
    --color-background: rgba(255, 255, 255, 0.1);
    
    /* Breakpoints */
    --breakpoint-mobile: 320px;
    --breakpoint-tablet: 768px;
    --breakpoint-desktop: 1024px;
}

* {
    margin: 0;
 	padding: 0;
 	box-sizing: border-box;
}

body {
    /* Full screen */
    width: 100vw;
    height: 100vh;
    /* Background */
    background: linear-gradient(135deg, rgb(124, 157, 150) 0%, rgb(236, 187, 143) 100%);
    background-attachment: fixed;
    /* Font & color */
    font-family: "Josefin Sans", sans-serif;
    color: wheat;   
    font-size: var(--space-md);
    /* Flexbox setting */
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-container {
    width: 100%;
    max-width: 700px;
    gap: var(--space-md);
    padding: var(--space-md);
    overflow: visible;
    /* Background and depth effect */
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3), 
            inset 0 1px rgba(255, 255, 255, 0.6), 
            0 0 20px rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: var(--base-spacing);
    /* Flexbox settings */
    display: flex;
    flex-direction: row;
}


.left-column {
    margin: var(--base-spacing);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
}

.logo {
    width: 100%;
    max-height: 12vh;
    object-fit: contain;
}

.name {
    width: 100%;
    font-size: var(--font-size-3xl);
}

.right-column{
    margin: var(--base-spacing);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.desc {
    width: 100%;
    /* max-width: 580px; */
    margin: 1rem 0 0 0;
    font-size: var(--font-size-lg);
    text-align: center;
}

