/* Mobile devices (<= 479px), portrait */
@media only screen and (max-width: 479px) and (orientation: portrait) {
    body {
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        font-size: var(--font-size-sm);
    }

    .card-container {
        width: 100%;
        min-height: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        border-radius: 0px;
        border: none;
    }

    .left-column, .right-column {
        margin-top: 0;
        width: 100%;
        height: auto;
        padding: 0px var(--space-xs);
    }

    .logo {
        width: 80%;
    }

    .desc {
        margin-top: var(--space-md);
        width: 80%;
    }
}

/* Mobile devices (landscape) */
@media only screen and (max-height: 500px) and (max-width: 767px) and (orientation: landscape) {
    .card-container {
        margin: 2rem;
        height: 60%;
        flex-direction: row;
        padding: var(--space-xs);
        gap: var(--space-xs);
    }

    .left-column {
        width: 40%; /* Set fixed proportion */
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: var(--space-xs);
    }

    .right-column {
        width: 60%;
        height: 100%;
        padding: var(--space-xs);
    }

    .logo {
        width: 100%; /* Take full width of parent */
        object-fit: contain;
    }

    .desc {
        margin-top: 1rem;
    }
}



