.flip-card {
    background-color: transparent;
    width: 75%;
    aspect-ratio: 3/4;
    position: relative;
    box-sizing: border-box;
    transform-style: preserve-3d;
    -webkit-perspective: 1000px;
    perspective: 1000px;
    -webkit-transform-style: preserve-3d;
    margin: 1rem 0;
    border-radius: 16px;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -webkit-transform: translate3d(0,0,0);
    transform: translateZ(0);
    will-change: transform;
}

.flip-loaded {
    opacity: 1;
}

.flip-loaded .flip-card-inner {
    animation: initialFlip 1.2s ease-in 1s;
}

@keyframes initialFlip {
    0% { transform: rotateY(0); }
    50% { transform: rotateY(180deg); }
    100% { transform: rotateY(0); }
}

.flip-card-inner {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    border-radius: 15px;
}

.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    box-sizing: border-box;
    border-radius: 15px;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}

.flip-card-front {
    background-image: url('https://i.imgur.com/oaQf1Gp.jpg'); 
    background-position: center;
    background-size: cover;
}

.flip-card-back {
    background-image: url('https://i.imgur.com/wUtHFy9.jpg'); 
    background-size: cover;
    background-position: bottom;
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

/* Mobile devices */
@media only screen and (max-width: 767px) {
    .flip-loaded .flip-card-inner {
        animation: initialFlip 0.8s ease-out 0.5s; 
    }

    @keyframes initialFlip {
        0% { transform: rotateY(0); }
        100% { transform: rotateY(180deg); } 
    }

    .flip-card-inner {
        transition: transform 0.4s ease-out; 
    }

    .flip-card {
        width: 40% !important;
        aspect-ratio: 3/4 !important;
    }
}